import $ from 'jquery';
import { HeaderTokenArgsI } from './types';
import { defaults, getToken } from './utils';

export default (args?: HeaderTokenArgsI) => {
  const {
    platDomain,
    platDomainKey,
    notSetPlatDomain,
    authCookieKey,
    authHeaderKey,
    authScheme,
    notSetAuthHeader,
  } = defaults(args);

  const token = getToken(authCookieKey, authScheme);

  /**
   * 1. 要求不设置
   * 2. cookie 中没有获取到 token
   * 3. 请求中已设值相应请求头
   * 时不再设置
   */
  if (!notSetAuthHeader && token) {
    $(document).ajaxSend((ev, xhr, settings) => {
      if (!settings || !settings.headers || !settings.headers[authHeaderKey]) {
        if (xhr && xhr.setRequestHeader && typeof xhr.setRequestHeader === 'function') {
          xhr.setRequestHeader(authHeaderKey, token);
        }
      }
    });
  }

  /**
   * 1. 要求不设置
   * 2. platDomain 为空值
   * 3. 请求中已经设置 [platDomainKey] 参数
   * 时不再设置
   */
  if (!notSetPlatDomain && platDomain) {
    $(document).ajaxSend((ev, xhr, settings) => {
      const hasPlatDomainParam = settings?.url?.indexOf(`${platDomainKey}=`) !== -1;

      if (!hasPlatDomainParam) {
        // eslint-disable-next-line no-param-reassign
        settings.url = `${settings.url}${
          settings?.url?.indexOf('?') !== -1 ? '&' : '?'
        }${platDomainKey}=${platDomain}`;
      }
    });
  }
};
