import axios from './axios';
import { HeaderTokenArgsI } from './types';

export default (args?: HeaderTokenArgsI) => {
  const NAME = 'tokenInterceptor';

  if (!window.angular) return NAME;

  window.angular
    .module(NAME, [])
    .factory(NAME, () => ({
      request(config: any) {
        if (config.url.indexOf('.tpl') !== -1) {
          // eslint-disable-next-line no-param-reassign
          config.withCredentials = false;
        }

        // 避免修改 templateCache 中的 url
        if (config.url.indexOf('.html') === -1) return axios(args)(config);

        return config;
      },
    }))
    .config([
      '$httpProvider',
      function config($httpProvider: any) {
        $httpProvider.interceptors.push(NAME);
      },
    ]);

  return NAME;
};
